<template>
  <div class="players md-content">
    <div class="md-layout" v-if="mounted">
      <div class="md-layout-item md-size-35">
        <md-table
          v-model="players"
          md-sort="name"
          md-sort-order="asc"
          md-card
          md-fixed-header
          @md-selected="onSelect"
        >
          <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
            <md-table-cell md-label="Игрок" md-sort-by="name">{{ item.name }}</md-table-cell>
          </md-table-row>
        </md-table>
      </div>
      <div class="md-layout-item">
        <div class="md-layout player-edit-header">
          <div class="md-layout-item" v-if="selected">{{selected.name}}</div>
          <div class="md-layout-item" v-if="!selected">*Новый игрок*</div>
        </div>
        <div class="md-layout previous-names">
          <div class="md-layout-item md-size-50">
            <h4>Предыдущие ники</h4>
            <md-table
              v-if="selected"
              v-model="selected.nameHistory"
              md-sort="date"
              md-sort-order="desc"
              md-card
              md-fixed-header
            >
              <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
                <md-table-cell md-label="Ник" md-sort-by="name">{{ item.name }}</md-table-cell>
                <md-table-cell
                  md-label="Дата смены"
                  md-sort-by="name"
                >{{ new Date(item.date).toISOString().slice(0,10) }}</md-table-cell>
              </md-table-row>
            </md-table>
          </div>
          <div class="md-layout-item">
            <div class="md-layout">
              <div class="md-layout-item">
                <md-field>
                  <label>{{ this.selected ? "Сменить ник" : "Введите ник" }}</label>
                  <md-input v-model="nameEditModel"></md-input>
                </md-field>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="newPlayer">Новый игрок</md-button>
              </div>
              <div class="md-layout-item edit-buttons">
                <md-button
                  class="md-raised md-primary last-button"
                  :disabled="nameEditModel==''"
                  @click="savePlayer"
                >Сохранить</md-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >
export default {
  name: "Players",
  props: { players: Array, databaseSettings: Object },
  data: () => ({
    //this variable workaround. Without v-if condition with it default sorting isn't work
    mounted: null,
    selected: null,
    nameEditModel: ""
  }),
  mounted() {
    this.mounted = true;
  },
  methods: {
    onSelect(item) {
      this.selected = item;
      if (item && !item.nameHistory) {
        item.nameHistory = [
          {
            name: item.name,
            date: Date.now()
          }
        ];
      }
    },
    newPlayer() {
      this.selected = null;
      this.nameEditModel = "";
    },
    savePlayer() {
      if (this.selected) {
        this.selected.name = this.nameEditModel;
        this.selected.nameHistory.push({
          nameChangeId: this.selected.nameHistory.length + 1,
          name: this.nameEditModel,
          date: Date.now()
        });
        this.nameEditModel = "";
      } else {
        this.databaseSettings.maxPlayerId++;
        var newPlayer = {
          id: this.databaseSettings.maxPlayerId,
          name: this.nameEditModel,
          nameHistory: [
            {
              nameChangeId: 1,
              name: this.nameEditModel,
              date: Date.now()
            }
          ]
        };
        this.players.push(newPlayer);

        this.nameEditModel = "";
        this.selected = newPlayer;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
::v-deep .md-tabs-content {
  height: calc(100vh - 50px) !important;
}

::v-deep .md-tab {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.previous-names td.md-table-cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 200px;
  max-width: 200px;
}

.md-layout-item {
  padding-left: 20px;
  padding-right: 20px;
  .search.selection.dropdown {
    margin-bottom: 3px;
  }
}

.players {
  .mx-datepicker {
    margin-bottom: 3px;
  }

  .player-edit-header {
    text-align: center;
    font-size: 1.3em;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .edit-buttons {
    text-align: right;
  }
  .first-button {
    margin-left: 0;
  }
  .last-button {
    margin-right: 0;
  }
}
</style>
