<template>
  <md-tabs>
    <md-tab id="tab-games" md-label="Игры">
      <div class="games md-content">
        <div class="md-layout">
          <div class="md-layout-item">
            <md-table
              v-model="games"
              md-sort="id"
              md-sort-order="desc"
              md-card
              md-fixed-header
              @md-selected="onSelect"
            >
              <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
                <md-table-cell md-label="№" md-numeric md-sort-by="id">{{ item.id }}</md-table-cell>
                <md-table-cell md-label="Дата" md-sort-by="date" class="date">{{ item.date }}</md-table-cell>
                <md-table-cell md-label="1">{{ getPlayerName(item.players[0]) }}</md-table-cell>
                <md-table-cell md-label="2">{{ getPlayerName(item.players[1]) }}</md-table-cell>
                <md-table-cell md-label="3">{{ getPlayerName(item.players[2]) }}</md-table-cell>
                <md-table-cell md-label="4">{{ getPlayerName(item.players[3]) }}</md-table-cell>
                <md-table-cell md-label="5">{{ getPlayerName(item.players[4]) }}</md-table-cell>
              </md-table-row>
            </md-table>
            <div class="md-layout">
              <div class="md-layout-item md-size-33">
                <md-field>
                  <label>Файл с данными игр</label>
                  <md-file @md-change="importDatabase($event)" accept="application/json" />
                </md-field>
              </div>
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="exportDatabase">Выгрузить базу</md-button>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised" @click="saveToStorage">Сохранить базу</md-button>
                <md-button class="md-raised" @click="loadFromStorage">Загрузить базу</md-button>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item md-size-33">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="databaseSettings.gamesMonth"
                      label="Месяц игр"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="databaseSettings.gamesMonth"
                    no-title
                    type="month"
                    locale="ru"
                  ></v-date-picker>
                </v-menu>
              </div>
              <div class="md-layout-item">
                <md-button class="md-raised" @click="newMonth">Новый месяц</md-button>
              </div>
            </div>
          </div>
          <div class="md-layout-item game">
            <div class="md-layout game-header">
              <div class="md-layout-item" v-if="selected.id">Игра №{{selected.id}}</div>
              <div class="md-layout-item" v-if="!selected.id">Новая Игра</div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <label>Игрок 1</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[0]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 2</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[1]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 3</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[2]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 4</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[3]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 5</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[4]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Игрок 6</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[5]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 7</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[6]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 8</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[7]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 9</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[8]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
                <label>Игрок 10</label>
                <model-list-select
                  :list="players"
                  v-model="selected.players[9]"
                  option-value="id"
                  option-text="name"
                  placeholder="выберите игрока"
                ></model-list-select>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <label>Дон</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.don"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Мафия 1</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.maf1"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Мафия 2</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.maf2"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Шериф</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.sherif"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field v-model="selected.date" label="Дата игры" readonly v-on="on"></v-text-field>
                  </template>
                  <v-date-picker v-model="selected.date" no-title locale="ru"></v-date-picker>
                </v-menu>
              </div>
              <div class="md-layout-item">
                <label>Первый убитый</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.firstKilled"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Лучший ход</label>
                <model-list-select
                  :list="firstKilledGuessPointsOptions"
                  v-model="selected.firstKilledGuessPoints"
                  option-value="value"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Победа</label>
                <model-list-select
                  :list="winGameOptions"
                  v-model="selected.cityWin"
                  option-value="value"
                  option-text="label"
                ></model-list-select>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="setControlsToNewGame">Новая игра</md-button>
              </div>
              <div class="md-layout-item edit-buttons">
                <md-button class="md-raised md-primary" @click="saveGame">Сохранить</md-button>
                <md-button class="md-raised last-button" @click="reloadEdit">Сбросить</md-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </md-tab>
    <md-tab id="tab-players" md-label="Игроки">
      <Players :players="players" :databaseSettings="databaseSettings" />
    </md-tab>
  </md-tabs>
</template>

<script >
import { ModelListSelect } from "vue-search-select";
import Players from "./Players.vue";
import "@mdi/font/css/materialdesignicons.css";

export default {
  name: "Games",
  components: {
    ModelListSelect,
    Players
  },
  data: () => ({
    databaseFile: { name: "" },
    filename: null,
    selectedDate: null,
    selected: {
      id: null,
      date: null,
      players: ["", "", "", "", "", "", "", "", "", ""],
      don: null,
      maf1: null,
      maf2: null,
      sherif: null,
      cityWin: null
    },
    selectPlayerOptions: [
      { index: 0, label: "1" },
      { index: 1, label: "2" },
      { index: 2, label: "3" },
      { index: 3, label: "4" },
      { index: 4, label: "5" },
      { index: 5, label: "6" },
      { index: 6, label: "7" },
      { index: 7, label: "8" },
      { index: 8, label: "9" },
      { index: 9, label: "10" }
    ],
    firstKilledGuessPointsOptions: [
      { value: 0, label: "0" },
      { value: 0.25, label: "0.25" },
      { value: 0.4, label: "0.4" }
    ],
    winGameOptions: [
      { value: true, label: "Мир" },
      { value: false, label: "Мафия" }
    ],
    databaseSettings: {
      gamesMonth: "",
      maxPlayerId: 0,
      maxGameId: 0
    },
    players: [],
    games: []
  }),
  created: function() {
    //this.loadFromStorage();
  },
  methods: {
    onSelect(item) {
      this.selected = this.copyGame(item);
    },
    newMonth() {
      this.games = [];
      this.databaseSettings.gamesMonth = new Date().toISOString().substr(0, 7);
      this.databaseSettings.maxGameId = 0;
      this.setControlsToNewGame();
    },
    getDatabase() {
      return {
        formatVersion: 1,
        settings: this.databaseSettings,
        games: this.games,
        players: this.players
      };
    },
    setDatabase(database) {
      this.games = database.games;
      this.players = database.players;
      this.databaseSettings = database.settings;
    },
    getPlayerName(id) {
      if (!id) {
        return "";
      }
      return this.players.find(x => x.id === id).name;
    },
    getEmptyGame() {
      return {
        id: null,
        date: null,
        players: ["", "", "", "", "", "", "", "", "", ""],
        don: null,
        maf1: null,
        maf2: null,
        sherif: null,
        firstKilled: null,
        firstKilledGuessPoints: null,
        cityWin: null
      };
    },
    copyGame(game) {
      return {
        id: game.id,
        date: game.date,
        players: game.players.slice(),
        don: game.don,
        maf1: game.maf1,
        maf2: game.maf2,
        sherif: game.sherif,
        firstKilled: game.firstKilled,
        firstKilledGuessPoints: game.firstKilledGuessPoints,
        cityWin: game.cityWin
      };
    },
    saveGame() {
      if (this.selected.id) {
        var game = this.games.find(x => x.id === this.selected.id);
        game.date = this.selected.date;
        game.players = this.selected.players;
        game.don = this.selected.don;
        game.maf1 = this.selected.maf1;
        game.maf2 = this.selected.maf2;
        game.sherif = this.selected.sherif;
        game.firstKilled = this.selected.firstKilled;
        game.firstKilledGuessPoints = this.selected.firstKilledGuessPoints;
        game.cityWin = this.selected.cityWin;

        this.selected = this.copyGame(game);
      } else {
        this.databaseSettings.maxGameId++;
        this.selected.id = this.databaseSettings.maxGameId;
        this.games.push(this.selected);
        this.selected = this.getEmptyGame();
      }
    },
    setControlsToNewGame() {
      this.selected = this.getEmptyGame();
    },
    reloadEdit() {
      if (this.selected.id) {
        var game = this.games.find(x => x.id === this.selected.id);
        this.selected = this.copyGame(game);
      } else {
        this.selected = this.getEmptyGame();
      }
    },
    exportDatabase() {
      var content = JSON.stringify(this.getDatabase());
      var fileName = this.databaseSettings.gamesMonth + "-games.json";
      this.downloadFile(content, fileName, "application/json");
    },
    downloadFile(content, fileName, contentType) {
      var a = document.createElement("a");
      var file = new Blob([content], { type: contentType });
      a.href = URL.createObjectURL(file);
      a.download = fileName;
      a.click();
    },
    test() {},
    importDatabase(e) {
      var reader = new FileReader();
      var self = this;
      reader.onload = function() {
        var database = JSON.parse(reader.result);
        self.setDatabase(database);
        this.setControlsToNewGame();
      };
      reader.readAsText(e[0]);
    },
    saveToStorage() {
      window.localStorage.setItem(
        "database",
        JSON.stringify(this.getDatabase())
      );
    },
    loadFromStorage() {
      var database = JSON.parse(window.localStorage.getItem("database"));
      this.setDatabase(database);
      this.setControlsToNewGame();
    }
  }
};
</script>

<style lang="scss" scoped>
::v-deep .md-tabs-content {
  height: calc(100vh - 50px) !important;
}

::v-deep .md-tab {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.md-layout-item {
  padding-left: 20px;
  padding-right: 20px;
  .search.selection.dropdown {
    margin-bottom: 3px;
  }
}

.game {
  .mx-datepicker {
    margin-top: 24px;
  }

  .game-header {
    text-align: center;
    font-size: 1.3em;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .edit-buttons {
    text-align: right;
  }
  .first-button {
    margin-left: 0;
  }
  .last-button {
    margin-right: 0;
  }
}
</style>
